import {action, observable} from "mobx";
import {getAsync} from "../../../../../common/fetch/getAsync";

interface IPaymentPosition{
    id: number,
    name: string,
    price: number
}

const state = observable({positions: null as unknown as IPaymentPosition[]})

const getPaymentPositionAsync = action(async () => {
    await getAsync("/Payment/GetPaymentPositions").then(response => {
        state.positions = response.data.accessPositions;
    })
})

const requestLavaPaymentLinkAsync = action( async (id: number) => {
    await getAsync(`/Payment/CreateLavaLink?id=${id}`).then(response => {
        if(response.status === 200)
            window.open(response.data, "_blank")
    })
})

const requestRuKassaPaymentLinkAsync = action(async (id: number) => {
    await getAsync(`/Payment/CreateRuKassaLink?id=${id}`).then(response => {
        if(response.status === 200)
            window.open(response.data, "_blank")
    })
})

export {state, getPaymentPositionAsync, requestLavaPaymentLinkAsync, requestRuKassaPaymentLinkAsync}