import {Button, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {closeLastModal} from "../../../../../component/modal/modalStore";

export const ContactModal = () => {
    return <>
        <ModalHeader className="px-5">
            <ModalTitle>Контакты</ModalTitle>
        </ModalHeader>
        <ModalBody className="px-5">
            <div className="text-start">
                <div>Email: lockvpn@outlook.com</div>
                <div>Telegram: <a href="https://t.me/lock_vpn">@lock_vpn</a></div>
            </div>
        </ModalBody>
        <ModalFooter>
            <Button variant="dark" onClick={closeLastModal}>Закрыть</Button>
        </ModalFooter>
    </>
}
