import React, {useRef} from "react";
import "./style.css";
import {Link, Navigate, useNavigate} from "react-router-dom";
import {Button, Form, FormGroup} from "react-bootstrap";
import {authState} from "../../../common/store/auth";
import {observer} from "mobx-react-lite";
import {FormProvider, useForm} from "react-hook-form";
import {TextInput} from "../../../common/component/input/text/TextInput";
import {PasswordInput} from "../../../common/component/input/password/PasswordInput";
import {authAsync, authWithGoogle} from "./store";
import {state} from "../../../common/appStore";
import HCaptcha from "@hcaptcha/react-hcaptcha";
import { useGoogleLogin } from "@react-oauth/google";
import {Google} from "react-bootstrap-icons";
import {errorToast} from "../../../common/toast/error";

export const Index = observer(() => {
    const hookForm = useForm()
    const navigation = useNavigate()
    const captchaRef = useRef<HCaptcha>(null)
    const loginWithGoogle = useGoogleLogin({
        onSuccess: async (response) => authWithGoogle(response.access_token),
        onError: () => errorToast("Произошла ошибка, попробуйте позднее")
    })

    if(authState.token !== null)
        return <Navigate to="/"/>

    return <div>
        <FormProvider {...hookForm}>
            <Form onSubmit={hookForm.handleSubmit((form) => authAsync(form))}>
                <div className="d-flex flex-column justify-content-center align-items-center vh-100">
                    <div className="d-flex flex-column align-items-center login-form">
                        <Link to="/" className="text-center text-decoration-none text-black mb-4">
                            <h1 className="fw-bold">LockVPN</h1>
                            <h5>Личный кабинет</h5>
                        </Link>

                        <TextInput label="Логин:"
                                   name="login"
                                   isRequired={true}
                                   placeholder="Введите логин"/>
                        <PasswordInput label="Пароль:"
                                       name="password"
                                       isRequired={true}
                                       placeholder="Введите пароль"/>

                        {state.mainStore?.captchaPublicKey &&
                            <FormGroup className="text-center">
                                <HCaptcha ref={captchaRef}
                                    sitekey={state.mainStore?.captchaPublicKey}
                                    onVerify={(token) => hookForm.setValue("token", token)}
                                />
                            </FormGroup>
                        }
                        <TextInput name="token"
                                   attributes={{className: "d-none"}}
                                   isRequired={true}/>

                        <Button variant="dark" type="submit" className="mt-2" onClick={captchaRef.current?.resetCaptcha}>
                            Войти
                        </Button>

                        <Button className="mt-2" variant="light" onClick={() => loginWithGoogle()}>
                            <Google className="me-2"/>
                            Войти с Google
                        </Button>

                        <Button variant="secondary" className="mt-3" onClick={() => navigation("/register")}>
                            Регистрация
                        </Button>
                        <Button variant="secondary" className="mt-2" onClick={() => navigation("/reset-password")}>
                            Восстановить пароль
                        </Button>
                    </div>
                </div>
            </Form>
        </FormProvider>
    </div>
})
