import React, {useState} from "react";
import {Slider} from "./Slider";
import {Price} from "./Price";
import {Feature} from "./Feature";


export const MainContent = () => {
    const [slideNumber, setSlideNumber] = useState<number>(0)
    const array = [
        <div className="d-flex flex-column justify-content-center align-items-center mx-4" style={{height: "30vh"}}>
            <h1>Низкая стоимость</h1>
            <h6>Тарифы стартуют от <span className="text-decoration-line-through">300руб</span> 200руб в месяц.</h6>
        </div>,
        <div className="d-flex flex-column justify-content-center align-items-center mx-4" style={{height: "30vh"}}>
            <h1>Высокая скорость </h1>
            <h6>Скорость подключения наших серверов начинается от 1гб/с.</h6>
        </div>,
        <div className="d-flex flex-column justify-content-center align-items-center mx-4" style={{height: "30vh"}}>
            <h1>7 локаций на выбор</h1>
            <h6>
                7 стран для подключения.
                <br/>
                А если нет нужной, просто оставьте заявку в личном кабинете, и мы добавим.
                <br/>
                (Нидерланды, Франция, Латвия, США, Россия, Турция, Финляндия)
            </h6>
        </div>
    ]

    const show = () => {

        setTimeout(() => {
            if (slideNumber < array.length - 1)
                setSlideNumber(slideNumber + 1)
            else
                setSlideNumber(0)
        }, 4500)

        return array[slideNumber]
    }

    return <>
        {/*{show()}*/}
        <Slider/>
        <Feature/>
        <Price/>
    </>
}
