import {Nav} from "react-bootstrap";
import {Link} from "react-router-dom";
import {
    CashStack,
    DoorOpenFill, EnvelopeFill,
    FileBinaryFill,
    FileEarmarkMedicalFill,
    FileEarmarkMinusFill,
    FileEarmarkPersonFill,
    GearFill,
    HddRackFill,
    HouseDoorFill,
    InfoCircleFill,
    ListTask,
    PeopleFill,
    PersonPlusFill
} from "react-bootstrap-icons";
import {observer} from "mobx-react-lite";
import {authState, removeToken} from "../../store/auth";
import {userRole} from "../../store/model/IUser";
import {openModal} from "../../component/modal/modalStore";
import {GooglePlay} from "./user/GooglePlay";
import {size} from "../../component/modal/IModal";
import {PrivacyPolicyModal} from "./guest/modal/privacy-policy/PrivacyPolicyModal";
import {TermsOfReturnModal} from "./guest/modal/terms-of-return/TermsOfReturnModal";
import {OfferAgreementModal} from "./guest/modal/offer-agreement/OfferAgreementModal";
import {ContactModal} from "./guest/modal/contacts/ContactModal";
import React from "react";

export const LeftMenu = observer(() => {

    return <>
        {/*Обычное меню - Начало*/}
        <div className="d-flex flex-column py-2 left-menu">
            <div className="d-none d-lg-block text-center">
                <div className="mx-auto mt-2">
                    <h4>LockVPN</h4>
                </div>
                <hr className="my-0 mt-2"/>
            </div>

            <Nav className="flex-column">
                <Link className="nav-link d-flex justify-content-between text-center align-items-center px-lg-5 py-3"
                      to="/">
                    <HouseDoorFill className="me-2" width={25} height={25} color="white"/>
                    <span className="w-100 text-center text-white d-none d-lg-block">Главная</span>
                </Link>
                <Link className="nav-link d-flex justify-content-between text-center align-items-center px-lg-5 py-3"
                      to="/settings">
                    <GearFill className="me-2" width={25} height={25} color="white"/>
                    <span className="w-100 text-center text-center text-white d-none d-lg-block">Настройки</span>
                </Link>
                <Link className="nav-link d-flex justify-content-between align-items-center px-lg-5 py-3" to="/tickets">
                    <InfoCircleFill className="me-2" width={25} height={25} color="white"/>
                    <span className="w-100 text-center text-white d-none d-lg-block">Поддержка</span>
                </Link>
                <div className="nav-link d-flex justify-content-between align-items-center px-lg-5 py-3"
                     style={{cursor: "pointer"}} onClick={removeToken}>
                    <DoorOpenFill className="me-2" width={25} height={25} color="white"/>
                    <span className="w-100 text-center text-white d-none d-lg-block">Выход</span>
                </div>
                <hr/>
                <div className="d-block d-lg-flex justify-content-around mx-lg-4" title="App Gallery">
                    <div className="mx-1 text-center my-auto p-2">
                        <a href="https://appgallery.huawei.com/app/C110194213" className="text-decoration-none"
                           target="_blank" rel="noreferrer">
                            <img src={`${window.location.origin}/img/appgallery-icon.svg`} alt="App Gallery" width={25}
                                 height={25}
                                 color="white"/>
                        </a>
                    </div>
                    <div className="mx-1 text-center my-auto p-2">
                        <a href="https://play.google.com/store/apps/details?id=com.lockvpnandroidapp" className="text-decoration-none"
                           target="_blank" rel="noreferrer">
                            <img src={`${window.location.origin}/img/google-play-icon.svg`} alt="Google Play" width={25}
                                 height={25}
                                 className="bg-white rounded-2"/>
                        </a>
                    </div>
                </div>
            </Nav>
            <hr/>
            <div>
                <div className="d-flex justify-content-center mx-1 text-center my-auto p-2"
                     style={{cursor: "pointer"}}
                     onClick={() => openModal({modal: <PrivacyPolicyModal/>, size: size.xl})}>
                    <FileEarmarkPersonFill width={25} height={25} className="d-block d-lg-none"/>
                    <span className="w-100 text-center text-white d-none d-lg-block">
                        Политика конфиденциальности
                    </span>
                </div>
                <div className="d-flex justify-content-center mx-1 text-center my-auto p-2"
                     style={{cursor: "pointer"}}
                     onClick={() => openModal({modal: <TermsOfReturnModal/>, size: size.xl})}>
                    <FileEarmarkMinusFill width={25} height={25} className="d-block d-lg-none"/>
                    <span className="w-100 text-center text-white d-none d-lg-block">
                        Условия возврата
                    </span>
                </div>
                <div className="d-flex justify-content-center mx-1 text-center my-auto p-2"
                     style={{cursor: "pointer"}}
                     onClick={() => openModal({modal: <OfferAgreementModal/>, size: size.xl})}>
                    <FileEarmarkMedicalFill width={25} height={25} className="d-block d-lg-none"/>
                    <span className="w-100 text-center text-white d-none d-lg-block">
                        Договор оферты
                    </span>
                </div>
                <div className="d-flex justify-content-center mx-1 text-center my-auto p-2"
                     style={{cursor: "pointer"}}
                     onClick={() => openModal({modal: <ContactModal/>, size: size.lg})}>
                    <EnvelopeFill width={25} height={25} className="d-block d-lg-none"/>
                    <span className="w-100 text-center text-white d-none d-lg-block">
                        Контакты
                    </span>
                </div>
            </div>

            {authState.user?.role === userRole.admin &&
                <>
                    <hr/>
                    <Nav className="flex-column mx-lg-4">
                        <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/users">
                            <PeopleFill className="me-2" color="white" width={25} height={25}/>
                            <span className="w-100 text-center text-white d-none d-lg-block">Пользователи</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/finance">
                            <CashStack className="me-2" color="white" width={25} height={25}/>
                            <span className="w-100 text-center text-white d-none d-lg-block">Финансы</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-between align-items-center"
                              to="/admin/location">
                            <HddRackFill className="me-2" color="white" width={25} height={25}/>
                            <span className="w-100 text-center text-white white d-none d-lg-block">Сервера</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/logs">
                            <ListTask className="me-2" color="white" width={25} height={25}/>
                            <span className="w-100 text-center text-white white d-none d-lg-block">Логи</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/file">
                            <FileBinaryFill className="me-2" color="white" width={25} height={25}/>
                            <span className="w-100 text-center text-white white d-none d-lg-block">Файлы</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-between align-items-center"
                              to="/admin/tickets">
                            <InfoCircleFill className="me-2" color="white" width={25} height={25}/>
                            <span className="w-100 text-center text-white white d-none d-lg-block">Обращения</span>
                        </Link>
                    </Nav>
                </>

            }
        </div>
        {/*Обычное меню - Конец*/}
    </>
})
