import React from "react";
import {Button} from "react-bootstrap";
import {useNavigate} from "react-router-dom";

export const Price = () => {
    const navigation = useNavigate()


    return <div className="d-block d-lg-flex justify-content-around w-100 bg-second py-lg-5">
        <div className="d-flex flex-column flex-md-row justify-content-md-around justify-content-center align-items-center flex-fill my-3 my-lg-0">
            <div className="price-box py-2 my-3 my-md-0">
                <div className="pb-2">
                    <h3 className="fw-bold">На 1 месяц</h3>
                </div>
                <div className="pb-2">
                    <div className="pb-2">200руб/мес</div>
                    <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                        Подключить за 200руб
                    </Button>
                </div>
            </div>
            <div className="price-box py-2 my-3 my-md-0">
                <div className="pb-2">
                    <h3 className="fw-bold">На 3 месяца</h3>
                </div>
                <div className="pb-2">
                    <div className="pb-2">180руб/мес</div>
                    <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                        Подключить за 540руб
                    </Button>
                </div>
            </div>
        </div>
        <div className="d-flex flex-column flex-md-row justify-content-md-around justify-content-center align-items-center flex-fill my-3 my-lg-0">
            <div className="price-box py-2 my-3 my-md-0">
                <div className="pb-2">
                    <h3 className="fw-bold">На 6 месяцев</h3>
                </div>
                <div className="pb-2">
                    <div className="pb-2">160руб/мес</div>
                    <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                        Подключить за 960руб
                    </Button>
                </div>
            </div>
            <div className="price-box py-2 my-3 my-md-0">
                <div className="pb-2">
                    <h3 className="fw-bold">На 1 год</h3>
                </div>
                <div className="pb-2">
                    <div className="pb-2">140руб/мес</div>
                    <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                        Подключить за 1680руб
                    </Button>
                </div>
            </div>
        </div>
    </div>
}