import {observer} from "mobx-react-lite";
import {
    Badge,
    Button, FormCheck,
    FormControl,
    FormGroup,
    FormLabel,
    ModalBody,
    ModalFooter,
    ModalHeader,
    ModalTitle
} from "react-bootstrap";
import {closeLastModal} from "../../../../../common/component/modal/modalStore";
import {useEffect, useState} from "react";
import {getPaymentPositionAsync, requestLavaPaymentLinkAsync, requestRuKassaPaymentLinkAsync, state} from "./store";
import Select from "react-select";

export const PaymentModal = observer(() => {
    useEffect(() => {
        getPaymentPositionAsync()
    }, []);

    return <>
        <ModalHeader>
            <ModalTitle>Продление подписки</ModalTitle>
        </ModalHeader>
        <ModalBody>
            <div className="text-center mb-3">
                <Badge bg="danger" className="text-wrap">При P2P оплате с использованием банковской карты может взыматься комиссия банка за перевод.</Badge>
                <Badge bg="success" className="mb-1">Рекомендованый способ оплаты - СБП</Badge>
            </div>
            <div className="mt-3 d-flex">
                <div className="w-100 text-center">
                    {
                        state.positions?.map(position => {
                            return <div key={position.id} className="d-flex mt-2 border-bottom border-1 mx-3">
                                <div className="w-100">
                                    {position.name}
                                </div>
                                <div className="w-100">
                                    {position.price + "руб."}
                                </div>
                                <div className="w-100">
                                    <FormCheck type="radio" name="access"/>
                                    {/*<Button size="sm"*/}
                                    {/*        variant="dark"*/}
                                    {/*        onClick={async () => await requestRuKassaPaymentLinkAsync(position.id)}>*/}
                                    {/*    Купить*/}
                                    {/*</Button>*/}
                                </div>
                            </div>
                        })
                    }
                </div>
                <div className="w-100 me-3">
                    <FormGroup>
                        <FormLabel>Промокод:</FormLabel>
                        <div className="d-flex">
                            <FormControl size="sm"
                                         className="me-2"/>
                            <Button variant="dark" size="sm">
                                Применить
                            </Button>
                        </div>
                    </FormGroup>

                    <hr/>

                    <div>
                        Срок действия до:
                    </div>
                    <div>
                        К оплате:
                    </div>

                </div>

            </div>
        </ModalBody>
        <ModalFooter>
            <Button variant="dark" size="sm" onClick={closeLastModal}>
                Закрыть
            </Button>
        </ModalFooter>
    </>
})