import {Link, useSearchParams} from "react-router-dom";
import React from "react";
import {HouseFill, PersonFill} from "react-bootstrap-icons";

export const TopMenu = () => {
    const [searchParams, setSearchParams] = useSearchParams()

    return <>
        <div className="d-flex h-100">
            <Link to="/"
                  className="d-flex align-items-center px-1 px-lg-3 h-100 text-decoration-none text-white horizontal-menu-item">
                <HouseFill className="d-block d-md-none me-2" width={25} height={25} color="white"/>
                <span className="d-none d-lg-block">Главная</span>
            </Link>
            <Link to="/login"
                  className="d-flex align-items-center px-1 px-lg-3 h-100 text-decoration-none text-white horizontal-menu-item">
                <PersonFill className="d-block d-md-none me-2" width={25} height={25} color="white"/>
                <span className="d-none d-lg-block">Личный кабинет</span>
            </Link>
        </div>

        <div className="d-flex mx-2 mx-lg-0">
            <div className="d-flex align-items-center ms-1 ms-lg-3">
                <a href="https://appgallery.huawei.com/app/C110194213"
                   className="text-decoration-none rounded-2 me-2 bg-white"
                   target="_blank" rel="noreferrer">
                    <img src={`${window.location.origin}/img/appgallery-icon.svg`}
                         alt="App Gallery"
                         width={30}
                         height={30}
                         color="white"/>
                </a>
                <a href="https://play.google.com/store/apps/details?id=com.lockvpnandroidapp"
                   className="text-decoration-none rounded-2 me-2 bg-white"
                   target="_blank" rel="noreferrer">
                    <img src={`${window.location.origin}/img/google-play-icon.svg`}
                         alt="App Gallery"
                         width={30}
                         height={30}
                         color="white"/>
                </a>
            </div>
        </div>
    </>
}